<template>
    <div>
        <!-- 구글로그인 : loginvue24 -->
        <!-- 로그인 버튼 -->
        <button v-if="!isLoggedIn" @click="login" class="google-button">
            <img src="@/assets/google-login.png" alt="Login with Google" class="google-button-img" />
        </button>
        <!-- 로그아웃 버튼 -->
        <button v-if="isLoggedIn" @click="logout" class="logout-button">Logout</button>
        
        <!-- 디버깅 정보 -->
        <div class="debug-info">
            <p>클라이언트 ID: {{ oauthClientId }}</p>
            <p>현재 도메인: {{ currentDomain }}</p>
            <p>로그인 상태: {{ isLoggedIn ? '로그인됨' : '로그아웃됨' }}</p>
        </div>
    </div>
</template>

<script setup>
import { ref, onMounted } from 'vue';

const oauthClientId = process.env.VUE_APP_OAUTH_CLIENT;
const isLoggedIn = ref(false);
const currentDomain = ref('');

onMounted(() => {
    currentDomain.value = window.location.origin;
    console.log('=== 구글 로그인 초기화 ===');
    console.log('클라이언트 ID:', oauthClientId);
    console.log('현재 도메인:', currentDomain.value);
});

const login = () => {
    console.log('=== 구글 로그인 시도 ===');
    
    if (!oauthClientId) {
        alert('OAuth 클라이언트 ID가 설정되지 않았습니다.');
        return;
    }

    // Google Identity Services가 로드될 때까지 대기
    const checkGoogleLoaded = () => {
        if (window.google && window.google.accounts && window.google.accounts.oauth2) {
            console.log('✅ Google Identity Services 로드 완료');
            
            try {
                // OAuth 2.0 클라이언트 초기화
                const client = window.google.accounts.oauth2.initCodeClient({
                    client_id: oauthClientId,
                    scope: 'email profile openid',
                    ux_mode: 'popup',
                    callback: handleAuthResponse
                });

                console.log('✅ OAuth 클라이언트 초기화 완료');
                client.requestCode();
            } catch (error) {
                console.error('❌ OAuth 클라이언트 초기화 오류:', error);
                alert('OAuth 클라이언트 초기화에 실패했습니다: ' + error.message);
            }
        } else {
            console.log('⏳ Google Identity Services 로드 대기 중...');
            setTimeout(checkGoogleLoaded, 100);
        }
    };

    checkGoogleLoaded();
};

const handleAuthResponse = (response) => {
    console.log('=== 구글 로그인 응답 ===');
    console.log('전체 응답:', response);
    
    if (response.code) {
        console.log('✅ 로그인 성공! 코드:', response.code);
        isLoggedIn.value = true;
        alert('로그인 성공!');
    } else if (response.error) {
        console.error('❌ 로그인 오류:', response.error);
        console.error('오류 설명:', response.error_description);
        alert('로그인에 실패했습니다: ' + response.error + (response.error_description ? '\n' + response.error_description : ''));
    } else {
        console.error('❌ 알 수 없는 응답:', response);
        alert('알 수 없는 응답을 받았습니다.');
    }
};

const logout = () => {
    console.log('=== 로그아웃 ===');
    isLoggedIn.value = false;
    alert('로그아웃되었습니다.');
};
</script>
<style scoped>
.google-button {
    background: none;
    border: none;
    cursor: pointer;
    padding: 0;
    margin-bottom: 20px;
}

.google-button-img {
    width: 200px;
    height: auto;
    display: block;
}

.google-button:hover {
    opacity: 0.9;
}

.logout-button {
    padding: 10px 20px;
    background-color: #4285f4;
    color: white;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    cursor: pointer;
    margin-bottom: 20px;
}

.logout-button:hover {
    background-color: #357ae8;
}

.debug-info {
    margin-top: 20px;
    padding: 15px;
    background-color: #f5f5f5;
    border-radius: 5px;
    font-family: monospace;
    font-size: 14px;
}

.debug-info p {
    margin: 5px 0;
    color: #666;
}
</style>
